.editable {
  box-sizing: border-box;
  color: inherit;
  display: block;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  min-height: 30px;
  min-width: 40px;
  position: relative; }
  .editable:not(.readonly) > div, .editable:not(.readonly) > p, .editable:not(.readonly) > span {
    cursor: pointer; }
  .editable > div, .editable > span, .editable > input, .editable > textarea {
    align-items: inherit;
    border: none;
    color: inherit;
    display: flex;
    flex: 1;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    height: 100%;
    max-height: 100%;
    justify-content: inherit;
    line-height: inherit;
    margin: 0;
    outline: none;
    overflow: hidden;
    padding: .25em;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    text-align: inherit;
    width: 100%;
    min-width: 40px; }
  .editable > div.multiline, .editable > textarea {
    align-items: flex-start;
    flex-direction: column;
    justify-content: flex-start;
    text-align: left; }
    .editable > div.multiline > p, .editable > textarea > p {
      margin: 0 0 .5rem 0;
      padding: 0; }
  .editable .placeholder {
    color: rgba(200, 200, 200, 0.8);
    font-style: italic; }
  .editable > input, .editable > textarea {
    -webkit-input-placeholder: rgba(200, 200, 200, 0.8);
    -moz-placeholder: rgba(200, 200, 200, 0.8);
    -moz-placeholder: rgba(200, 200, 200, 0.8);
    -ms-input-placeholder: rgba(200, 200, 200, 0.8); }
  .editable:focus-within {
    outline: -webkit-focus-ring-color auto 1px; }
  .editable input[type="checkbox"] {
    height: 1em;
    min-width: auto;
    width: 1em;
    margin: auto; }
