* {
  box-sizing: border-box; }

html {
  overflow: hidden; }

body {
  font-family: Arial, Calibri, sans-serif;
  font-family: var(--font-primary);
  margin: 0;
  padding: 0; }

a {
  color: #08f;
  text-decoration: none; }
  a:hover {
    text-decoration: underline; }
  a.missing {
    color: #c44; }

blockquote {
  background: oldlace;
  border-color: #f5cf88;
  border-radius: 5px;
  border-style: solid;
  border-width: 0 0 0 4px;
  box-shadow: 3px 3px 3px #00000033;
  margin: 1rem 1.5rem;
  padding: .5rem .75rem;
  text-align: justify; }

button {
  border: 1px outset #66666655;
  border-radius: .25rem;
  box-shadow: 0 0 0 1px #66666655;
  cursor: pointer;
  padding: .25rem .4rem; }
  button:not(.link) {
    background: #ffffff55;
    background: -webkit-linear-gradient(top, #ffffff55 0%, #ffffff55 20%, #ffffff00 80%, #ffffff00 100%);
    background: linear-gradient(to bottom, #ffffff55 0%, #ffffff55 20%, #ffffff00 80%, #ffffff00 100%); }
  button:focus {
    box-shadow: 0 0 0 3px rgba(226, 251, 251, 0.5); }
  button.danger {
    background-color: #c44;
    color: white; }
  button.safe {
    background-color: #97cd76;
    color: #fff;
    font-weight: bold;
    text-shadow: 1px 1px 1px gray; }
  button.link {
    border: none;
    box-shadow: none; }
  button[disabled], button.disabled {
    background-color: #eeeeee;
    color: #aaaaaa;
    text-shadow: none; }

fieldset {
  border: 1px solid currentColor;
  margin: 5px; }
  fieldset > legend {
    text-shadow: 2px 2px 3px #00000033;
    font-weight: bold; }

h1, h2, h3, h4, h5, h6 {
  border-bottom: 1px solid var(--color-primary);
  font-family: var(--font-for-titles);
  margin-bottom: .25rem;
  padding-bottom: .25rem; }

hr {
  border-color: var(--color-primary);
  border-style: solid;
  border-width: 1px 0 0 0;
  margin: .5rem 0; }

input, textarea {
  border-radius: 5px;
  border: 1px solid rgba(1, 1, 1, 0.25);
  flex: 1;
  outline: none;
  margin: .25rem;
  padding: .25rem; }
  input:focus, textarea:focus {
