label {
  font-size: .8rem; }

p {
  text-align: justify; }

select {
  background-color: #fff;
  border-radius: 5px;
  flex: 1;
  margin: .25rem;
  padding: .1rem; }

svg {
  fill: currentColor;
  height: 1rem;
  shape-rendering: geometricPrecision;
  width: 1rem; }
  svg.x2 {
    height: 2rem;
    width: 2rem; }

table {
  border: none;
  border-collapse: separate;
  border-spacing: 0;
  box-shadow: 3px 3px 3px #00000033;
  padding: 0; }
  table th {
    color: var(--color-primary-text);
    font-family: var(--font-for-text);
    position: relative;
    z-index: 0; }
    table th:before {
      background: var(--color-primary);
      content: '';
      height: 100%;
      opacity: .7;
      position: absolute;
      width: 100%;
      left: 0;
      top: 0;
      z-index: -1; }
  table th, table td {
    border-color: #00000033;
    border-style: groove;
    border-width: 1px 1px 0 0;
    padding: .2rem; }
    table th:first-child, table td:first-child {
      border-left-width: 1px; }
  table > thead:last-child > tr:last-child > th, table > thead:last-child > tr:last-child > td, table > thead:last-child > th, table > thead:last-child > td, table > tbody:last-child > tr:last-child > th, table > tbody:last-child > tr:last-child > td, table > tbody:last-child > th, table > tbody:last-child > td, table > tfoot:last-child > tr:last-child > th, table > tfoot:last-child > tr:last-child > td, table > tfoot:last-child > th, table > tfoot:last-child > td, table > tr:last-child > tr:last-child > th, table > tr:last-child > tr:last-child > td, table > tr:last-child > th, table > tr:last-child > td {
    border-bottom-width: 1px; }
  table thead th:before {
    opacity: 1; }

.callout, [class*="callout-"] {
  background-color: #fff;
  border-color: #00000033;
  border-style: solid;
  border-width: 1px;
  margin: 1em 0 1em 25px;
  padding: .25rem;
  position: relative; }
  .callout.centered, [class*="callout-"].centered {
    left: 13px;
    margin: auto; }
  .callout:first-child, [class*="callout-"]:first-child {
    margin-top: 0; }
  .callout:before, [class*="callout-"]:before {
    border: 1px solid #00000033;
    content: '';
    display: inline-block;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 16px 16px;
    position: absolute;
    left: -25px;
    top: -1px;
    height: 100%;
    width: 24px; }
  .callout[class*="-book"], [class*="callout-"][class*="-book"] {
    background-color: #edfdfd; }
    .callout[class*="-book"] th, [class*="callout-"][class*="-book"] th {
      color: #083d3d; }
      .callout[class*="-book"] th:before, [class*="callout-"][class*="-book"] th:before {
        background-color: #e2fbfb;
        opacity: 1; }
    .callout[class*="-book"]:before, [class*="callout-"][class*="-book"]:before {
      background-color: #d7f9f9;
      background-image: var(--image-book); }
  .callout[class*="-combo"], [class*="callout-"][class*="-combo"] {
    background-color: #e5f3dd; }
    .callout[class*="-combo"] th, [class*="callout-"][class*="-combo"] th {
      color: #131f0b; }
      .callout[class*="-combo"] th:before, [class*="callout-"][class*="-combo"] th:before {
        background-color: #deefd3;
        opacity: 1; }
    .callout[class*="-combo"]:before, [class*="callout-"][class*="-combo"]:before {
      background-color: #d7ecca;
      background-image: var(--image-combo); }
  .callout[class*="-info"], [class*="callout-"][class*="-info"] {
    background-color: #edfdfd; }
    .callout[class*="-info"] th, [class*="callout-"][class*="-info"] th {
      color: #083d3d; }
      .callout[class*="-info"] th:before, [class*="callout-"][class*="-info"] th:before {
        background-color: #e2fbfb;
        opacity: 1; }
    .callout[class*="-info"]:before, [class*="callout-"][class*="-info"]:before {
      background-color: #d7f9f9;
      background-image: var(--image-info); }
  .callout[class*="-system"], [class*="callout-"][class*="-system"] {
    background-color: #fefaf2; }
    .callout[class*="-system"] th, [class*="callout-"][class*="-system"] th {
      color: #452f06; }
      .callout[class*="-system"] th:before, [class*="callout-"][class*="-system"] th:before {
        background-color: #fdf5e6;
        opacity: 1; }
    .callout[class*="-system"]:before, [class*="callout-"][class*="-system"]:before {
      background-color: #fcf0da;
      background-image: var(--image-system); }

table[class*="callout-"] {
  border: none;
  box-shadow: 1px 3px 3px #00000022, 3px 1px 3px #00000022;
  padding: 0; }
  table[class*="callout-"]:before {
    box-shadow: 0 3px 3px #00000033;
    border-right: none;
    border-radius: 5px 0 0 5px;
    height: calc(100% - 2px);
    top: 0; }

.layout.small .callout, .layout.small [class*="callout-"] {
  box-shadow: none;
  max-width: calc(100% - 30px); }
  .layout.small .callout:before, .layout.small [class*="callout-"]:before {
    box-shadow: none; }

.center {
  justify-content: center;
  text-align: center; }

.danger {
  border-color: #d76c6c;
  color: #c44; }

.dropshadow {
  box-shadow: 2px 2px 5px #d3d6db;
  box-shadow: 2px 2px 5px var(--color-shadow); }

.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.font-for-titles {
  font-family: var(--font-for-titles); }

.font-for-text {
  font-family: var(--font-for-text); }

.hidden {
  display: none !important; }

.highlight {
  background-color: #e2fbfb;
  color: #333;
  font-size: inherit; }

.input-wrapper {
  align-items: center;
  display: flex;
  flex: 1;
  flex-direction: row;
  font-weight: bold;
  padding: 0 .25rem; }
  .input-wrapper > label {
    font-size: .8rem; }
  .input-wrapper > input {
