.info-box {
  box-shadow: 2px 2px 5px #d3d6db;
  box-shadow: 2px 2px 5px var(--color-shadow);
  background: var(--color-background);
  border: 2px solid var(--color-primary);
  box-shadow: 3px 3px 3px #00000033, 0 0.75em 0 var(--color-background), -0.75em 0 0 var(--color-background);
  clear: both;
  float: right;
  font-size: .9rem;
  margin: 0 0 1em 1em;
  position: relative;
  width: 275px;
  z-index: 1; }

table.info-box {
  border: 1px solid #00000022; }
  table.info-box th {
    color: #333333; }
    table.info-box th:before {
      background: #00000022; }
  table.info-box th, table.info-box td {
    border: 1px groove #00000033; }
  table.info-box img {
    width: 100%; }

.layout.small .info-box {
  float: none;
  margin: 0 0 1em 0;
  width: 100%; }

.inset {
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  background-color: #565656;
  color: transparent;
  text-shadow: 1px 1px 2px #ffffffbb; }

.lightbox {
  border: 1px solid var(--color-primary);
  border-radius: 5px;
  box-shadow: 3px 3px 3px #00000033;
  display: flex;
  flex-direction: column;
  padding: 5px; }
  .lightbox > .title {
    align-items: center;
    background: var(--color-primary);
    color: var(--color-primary-text);
    display: flex;
    font-weight: bold;
    margin: -5px -5px 5px -5px;
    padding: 8px; }
    .lightbox > .title > svg {
      margin-right: 5px; }

.loading {
  overflow: hidden; }
  .loading:after {
    align-items: center;
    background-color: #ffffffdd;
    color: #000000aa;
    content: 'Loading...';
    display: flex;
    font-style: italic;
    font-weight: bold;
    height: 100%;
    justify-content: center;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%; }

.row {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: flex-start; }
  .row > * {
    margin: .25rem; }

.warning {
  align-items: center;
  background: var(--color-warning-bg);
  border-radius: 5px;
  border: 2px solid var(--color-warning);
  color: var(--color-warning-text);
  display: flex;
  margin: 0.5rem 0;
  padding: 5px; }
  .warning > svg {
    margin-right: .25rem; }

