.sheet-list .message {
  display: flex;
  justify-content: center; }

.sheet-list .filter {
  margin: .5rem .25rem .25rem;
  width: calc(100% - .5rem); }

.sheet-list .scrollable {
  height: 200px !important; }

.sheet-list table {
  box-shadow: none;
  margin: 5px auto 0;
  table-layout: fixed;
  width: calc(100% - 10px); }
  .sheet-list table td.properties {
    padding: 0;
    text-align: center;
    width: 52px; }
  .sheet-list table .toggle {
    cursor: auto;
    height: 15px;
    margin: 5px;
    padding: 0;
    vertical-align: middle;
    width: 15px; }
    .sheet-list table .toggle.on {
      color: #97cd76;
      stroke: none; }
    .sheet-list table .toggle.off {
      fill: #ddd;
      stroke: none; }
