.wiki.layout {
  background: #f6f6f6;
  background: var(--color-background);
  display: flex;
  flex: 1;
  flex-direction: column;
  height: 100vh;
  margin: 0;
  max-height: 100vh;
  max-width: 100vw;
  min-height: 100vh;
  min-width: 100vw;
  padding: 0;
  width: 100vw; }
  .wiki.layout > .header {
    align-items: center;
    background-color: white;
    background-color: var(--color-primary);
    border-bottom: 1px outset;
    box-shadow: 0px 2px 5px rgba(211, 214, 219, 0.75);
    box-shadow: 0px 2px 5px var(--color-shadow);
    color: black;
    color: var(--color-primary-text);
    display: flex;
    justify-content: flex-start;
    min-height: 40px;
    width: 100%; }
    .wiki.layout > .header .navigation.toggle {
      background: inherit;
      border-bottom-width: 2px;
      border: 1px solid transparent;
      color: inherit;
      cursor: pointer;
      display: none;
      margin-bottom: -4px;
      margin-left: .5rem;
      padding: .5rem; }
      .wiki.layout > .header .navigation.toggle * {
        pointer-events: none; }
    .wiki.layout > .header > .logo {
      align-items: center;
      box-sizing: border-box;
      cursor: pointer;
      display: flex;
      justify-content: flex-start;
      min-width: 150px; }
      .wiki.layout > .header > .logo > .nerd, .wiki.layout > .header > .logo > .rage {
        font-size: 1.4em;
        text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.8), -1px 1px 0 rgba(255, 255, 255, 0.8), 1px -1px 0 rgba(255, 255, 255, 0.8), -1px -1px 0 rgba(255, 255, 255, 0.8), 1px 1px 3px #666, -1px 1px 3px #666, 1px -1px 3px #666, -1px -1px 3px #666; }
      .wiki.layout > .header > .logo .nerd {
        color: #000; }
      .wiki.layout > .header > .logo .rage {
        color: #f00; }
    .wiki.layout > .header > .navigation.toggle {
      order: 0; }
    .wiki.layout > .header > .search-box {
      order: 1; }
      .wiki.layout > .header > .search-box:focus-within ~ * {
        display: none; }
    .wiki.layout > .header > .user-menu {
      order: 2; }
  .wiki.layout > .content {
    display: flex;
    flex-direction: row;
    flex: 1;
    position: relative;
    height: 100%;
    width: 100%; }
    .wiki.layout > .content > .navigation {
      max-height: calc(100vh - 40px);
      max-width: 175px;
      width: 175px;
      z-index: 1; }
    .wiki.layout > .content > .page {
      flex: 1;
      max-height: calc(100vh - 40px);
      width: 100%; }
    .wiki.layout > .content > .navigation ~ .page {
      width: calc(100vw - 175px); }
  .wiki.layout.small {
    font-size: 10pt; }
    .wiki.layout.small > .header > .logo {
      min-width: auto; }
      .wiki.layout.small > .header > .logo > .nerd, .wiki.layout.small > .header > .logo > .rage {
        display: none; }
    .wiki.layout.small > .header > .navigation.toggle {
      display: flex; }
    .wiki.layout.small.collapse-navigation > .content > .navigation {
      display: none; }
    .wiki.layout.small.expand-navigation > .header > .navigation.toggle {
      background: var(--color-background);
      border-bottom-color: var(--color-background);
      box-shadow: 5px 0 5px #33333355;
      color: var(--color-primary);
      margin-bottom: -6px; }
    .wiki.layout.small.expand-navigation > .content > .navigation {
      bottom: 0;
      box-shadow: 3px 5px 5px #33333355;
      left: 0;
      position: absolute !important;
      top: 0;
      width: 85%;
      z-index: 5; }
    .wiki.layout.small button {
      font-size: 10pt; }
  .wiki.layout.no-campaign > .header {
    justify-content: flex-end; }
    .wiki.layout.no-campaign > .header > .logo {
      flex: 1; }
    .wiki.layout.no-campaign > .header > .navigation.toggle, .wiki.layout.no-campaign > .header > .search-box {
      display: none; }
