.navigation {
  background: var(--color-background);
  border-right: 1px solid #42afe3;
  border-right: 1px solid var(--color-primary);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  overflow: auto; }
  .navigation > .link-sections > div > .content {
    padding: 8px;
    width: 175px; }
    .navigation > .link-sections > div > .content > .active {
      align-items: center;
      display: flex;
      justify-content: space-between;
      overflow: hidden;
      padding: .1rem;
      text-overflow: ellipsis;
      white-space: nowrap; }
      .navigation > .link-sections > div > .content > .active > svg {
        margin-right: .25rem;
        order: 1; }
    .navigation > .link-sections > div > .content .list-title {
      align-items: center;
      display: flex;
      justify-content: flex-start; }
      .navigation > .link-sections > div > .content .list-title > svg {
        margin-right: .25rem;
        margin-left: -.35rem; }
    .navigation > .link-sections > div > .content > .campaign-nav > ul, .navigation > .link-sections > div > .content > ul {
      list-style: none;
      margin: 0;
      padding: .1rem 0 1rem .75rem; }
    .navigation > .link-sections > div > .content li {
      overflow: hidden;
      padding: .1rem;
      position: relative;
      text-overflow: ellipsis;
      white-space: nowrap; }
      .navigation > .link-sections > div > .content li.section {
        margin-left: -.75rem; }
      .navigation > .link-sections > div > .content li > .controls {
        background: #ffffff;
        display: none;
        height: 100%;
        position: absolute;
        right: 0;
        top: 0; }
        .navigation > .link-sections > div > .content li > .controls > .delete, .navigation > .link-sections > div > .content li > .controls > .edit {
          cursor: pointer;
          margin: 0 .25rem; }
        .navigation > .link-sections > div > .content li > .controls > .delete {
          color: var(--color-danger); }
      .navigation > .link-sections > div > .content li:hover > .controls {
        align-items: center;
        display: flex;
        justify-content: center; }
  .navigation > .footer {
    padding: 5px;
    text-align: center; }
    .navigation > .footer > .buttons {
      margin: 5px 0; }
    .navigation > .footer > .add {
      margin: 5px;
      max-width: calc(100% - 10px); }

.layout.small .navigation > .link-sections > div > .content li .controls {
  display: none; }
