.color-picker.input {
  align-items: center;
  flex: 1;
  height: 100%;
  justify-content: center;
  padding: .25rem;
  position: relative; }
  .color-picker.input > .swatch {
    height: 100%;
    width: 100%; }
  .color-picker.input > .github-picker {
    justify-content: center;
    left: 0;
    margin-top: 8px;
    position: absolute !important;
    right: 0;
    z-index: 1; }
  .color-picker.input.readonly > .swatch {
    cursor: auto; }
