.layout.medium .campaign.page > .contents > div, .layout.small .campaign.page > .contents > div {
  display: block; }
  .layout.medium .campaign.page > .contents > div > .column, .layout.small .campaign.page > .contents > div > .column {
    flex: auto;
    width: 100%; }
