.search-box {
  align-items: center;
  border-radius: 5px;
  display: flex;
  flex: 1;
  justify-content: center;
  margin: 5px;
  position: relative; }
  .search-box > .search.icon {
    fill: #ccc;
    position: absolute;
    left: .75rem; }
  .search-box:focus-within > .search.icon {
    fill: currentColor; }
  .search-box > .input {
    flex: 1;
    padding: .25rem .25rem .25rem 1.75rem;
    width: 100%; }
  .search-box.open > input {
    border: 1px solid #00000044;
    border-radius: 5px 5px 0 0;
    box-shadow: 3px 3px 3px #33333333; }
  .search-box.open > .search-results {
    background: #fff;
    border-radius: 0 0 5px 5px;
    border: 1px solid #00000044;
    border-top-color: transparent;
    box-shadow: 3px 3px 3px #33333333;
    font-size: .8rem;
    left: 0;
    line-height: 1.5rem;
    list-style: none;
    margin: -5px 4px;
    padding: .25rem;
    position: absolute;
    right: 0;
    top: 100%;
    z-index: 5; }
    .search-box.open > .search-results > .search-result {
      cursor: pointer;
      padding: .15rem; }
      .search-box.open > .search-results > .search-result[aria-selected="true"] {
        background: var(--color-system); }
      .search-box.open > .search-results > .search-result > .preview {
        font-size: .8rem; }
