.participants.lightbox {
  height: 250px;
  position: relative; }
  .participants.lightbox.loading:after {
    content: 'Updating...'; }
  .participants.lightbox .participant-list {
    list-style: none;
    margin: 0;
    max-width: calc(100% - 15px);
    padding: 5px; }
    .participants.lightbox .participant-list > .participant.row {
      justify-content: space-between;
      margin-bottom: .25rem; }
      .participants.lightbox .participant-list > .participant.row:last-child {
        margin-bottom: 0; }
      .participants.lightbox .participant-list > .participant.row > .nerd.icon {
        height: 2em;
        margin: 0 .25rem 0 0;
        width: 2em; }
      .participants.lightbox .participant-list > .participant.row > .display {
        align-items: center;
        display: flex;
        flex: 1;
        overflow: hidden; }
        .participants.lightbox .participant-list > .participant.row > .display > .name, .participants.lightbox .participant-list > .participant.row > .display > .username {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap; }
        .participants.lightbox .participant-list > .participant.row > .display > .username {
          font-size: .8rem;
          margin-left: .5rem;
          opacity: .6; }
      .participants.lightbox .participant-list > .participant.row > .role {
        opacity: .5; }
      .participants.lightbox .participant-list > .participant.row > .remove.icon {
        cursor: pointer;
        opacity: .5; }
      .participants.lightbox .participant-list > .participant.row.added {
        background-color: rgba(151, 205, 118, 0.2);
        color: #62a23b; }
      .participants.lightbox .participant-list > .participant.row.updated {
        background-color: rgba(181, 245, 245, 0.5); }
      .participants.lightbox .participant-list > .participant.row.removed {
        background-color: rgba(204, 68, 68, 0.2);
        color: #862424;
        text-decoration: line-through; }
      .participants.lightbox .participant-list > .participant.row:hover > .role, .participants.lightbox .participant-list > .participant.row:hover > .remove.icon {
        opacity: 1; }
  .participants.lightbox > .user.search-box {
    margin: 5px 0 0 0; }
  .participants.lightbox > button.safe {
    margin: 5px; }

.layout.small .participants > .participant-list > .participant.row > .role, .layout.small .participants > .participant-list > .participant.row > .remove.icon {
  display: inline-block; }
