.theme-editor.lightbox > .title {
  margin-bottom: 5px; }

.theme-editor > .input-wrapper > label {
  font-weight: normal;
  padding: .5rem;
  text-align: right;
  width: 85px; }

.theme-editor > .input-wrapper > .search-box, .theme-editor > .input-wrapper > .display {
  margin: 0; }

.theme-editor > .input-wrapper .swatch {
  border: none;
  font-weight: bold; }

.theme-editor > .input-wrapper .primary > .swatch {
  color: var(--color-primary-text); }

.theme-editor > .input-wrapper.font-for-titles > label {
  font-size: 1rem;
  font-weight: bold; }
